@mixin bubbles-sm($x: 0, $y: 0) {
    background-image: url('../img/bubbles-left.png');
    background-position: $x $y;
    background-repeat: no-repeat;
}

.bubbles-sm {
    background-image: url('../img/bubbles-sm.png');
    background-position: -15px -15px;
    // background-size: ;
    background-repeat: no-repeat;
}


.main-header {
    @include mq(md) {
        @include bubbles(60px , 0);
    }
    @include mq(lg) {
        @include bubbles(200px, 0);
    }
}

.navbar-bottom {
    @include bubbles(0, 0);
    @include mq(md) {
        @include bubbles(60px, -78px);
    }
    @include mq(lg) {
        @include bubbles(200px, -100px);
    }
}

body {
    position: relative;
    @include mq(md) {
        @include bubbles(60px, 0);
    }
    @include mq(lg) {
        @include bubbles(200px, 0);
    }
}
